// import logo from './logo.svg';
import { queryAllByDisplayValue } from '@testing-library/react';
import React,{ useState } from 'react';
import './App.css';
import One from './component/One';
import Two from './component/Two';

function App() {


  let conArr = {
    "One":<One />,
    "Two":<Two />,
  }

  console.log(conArr)
  let [cpnt,setCpnt] = useState(conArr['One']);

  function nav(e){
    e.preventDefault();

    var nav = document.querySelectorAll('header a');
    nav.forEach(function(i){
      i.classList.remove('active');
    });
    e.target.classList.add('active');

    var clkNav = e.target.textContent; 
    console.log(clkNav)

    setCpnt(conArr[clkNav]);
  }

  return (
    <>
      <header>
        <a onClick={nav}>One</a>
        <a onClick={nav}>Two</a>
      </header>
      <main>
          {cpnt}
      </main>
    </>
  );
}

export default App;